/* components/HeroCarousel/HeroCarousel.module.css */
.heroCarousel {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.carouselWrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.bgCarousel {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.videos {
  position: relative;
  width: 100%;
  height: 100%;
}

.videoMask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  will-change: transform;
}

.videoMask.active {
  z-index: 2;
}

.videoMask.prev {
  z-index: 1;
}

.cover {
  overflow: hidden;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.fgCarousel {
  position: absolute;
  width: 100%;
  height: 100%;
}

.textCarousel {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
}

.textCarouselItem {
  text-align: center;
  color: white;
}

.siteHeadline {
  font-size: 6vw;
  font-weight: 400;
  letter-spacing: -0.02em;
  line-height: 1;
  margin-bottom: 2rem;
}

.char {
  display: inline-block;
  will-change: transform;
}

.space {
  margin: 0 0.2em;
}

.caption {
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.next {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
}

.nextTitle {
  margin-top: 0.5rem;
  font-weight: 500;
}

.progressBar {
  height: 1px;
  width: 100%;
  background: rgba(255, 255, 255, 0.2);
  margin-top: 1rem;
  position: relative;
}

.progressBar::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: calc(var(--p) * 1%);
  background: white;
  visibility: var(--v);
}

.cta {
  position: absolute;
  bottom: 10%;
  right: 5%;
  color: white;
  pointer-events: auto;
  cursor: pointer;
}

.nav {
  position: absolute;
  bottom: 5%;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
  z-index: 4;
}

.nav button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 1rem;
}

.progress {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 0.5rem;
  z-index: 3;
}

.cursorOutline,
.cursorLabel {
  position: fixed;
  pointer-events: none;
  z-index: 10;
  color: white;
  transform: translate3d(0, 0, 0);
}

.cursorOutline {
  border: 1px solid white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
}

.cursorLabel {
  font-size: 0.875rem;
}